.marketContainer {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  align-items: center;
  margin: 20px 0px;
}
.marketContainer .marketIcon {
  width: 32px;
  height: 32px;
  margin: 0 auto;
}
.marketContainer .marketTableText {
  margin: auto 0;
  color: #fff;
}
.marketContainer .marketTableUrl {
  color: #1B70F1;
  text-decoration: none;
}/*# sourceMappingURL=Market.module.css.map */